<template>
  <div class="forgot">
    <van-nav-bar title="找回密码" left-text="返回" left-arrow @click-left="back" />
    <div>
      <div v-if="!isNext">
        <div class="list">
          <van-form>
            <van-field v-model="first.email" type="email" label="邮箱" placeholder="请输入邮箱地址" />
            <van-field
              key="code"
              v-model="first.code"
              type="text"
              label="验证码"
              placeholder="请输入6位验证码"
            >
              <template #button>
                <van-button
                  size="small"
                  type="info"
                  @click="sendCode"
                  :disabled="isSend"
                  round
                >{{text}}</van-button>
              </template>
            </van-field>
          </van-form>

          <div class="button">
            <van-button round block type="info" @click="next">下一步</van-button>
          </div>
        </div>
      </div>
      <div v-else>
        <div class="else">
          <van-form>
            <van-field
              v-model="userInfo.phone"
              type="text"
              label="手机号"
              placeholder="请输入注册时的手机号"
              left-icon="phone-o"
              maxlength="11"
            />
            <van-field
              v-model="userInfo.password"
              :type="isEye ? 'text' : 'password'"
              label="新密码"
              placeholder="密码至少包含数字和英文(6-20位)"
              left-icon="lock"
              :right-icon="isEye ? 'eye-o' : 'closed-eye'"
              @click-right-icon="toggle"
              maxlength="16"
            />
          </van-form>
          <div class="v-button">
            <van-button round block type="info" native-type="submit" @click="commit">提交</van-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Testing from "../assets/js/testing";
export default {
  data() {
    return {
      isNext: false,
      first: {
        email: "",
        code: ""
      },
      userInfo: {
        phone: "",
        password: ""
      },
      isEye: false,
      text: "发送验证码",
      isSend: false
    };
  },
  methods: {
    back() {
      if (this.isNext) {
        this.isNext = false;
      } else {
        this.$router.go(-1);
      }
    },
    toggle() {
      this.isEye = !this.isEye;
    },

    //下一步
    next() {
      if (!Testing.Testing(this.first)) {
        return;
      }
      //发送验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/checkValidCode",
        data: {
          appkey: this.appkey,
          validCode: this.first.code
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == "K001") {
            this.isNext = true;
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    //发送验证码
    sendCode() {
      if (!Testing.Testing({ email: this.first.email })) {
        return;
      }

      //获取邮箱验证码
      let time = 60;
      this.text = time + "秒后重新发送";
      this.isSend = true;
      let timer = setInterval(() => {
        if (time == 0) {
          clearInterval(timer);
          timer = null;
          this.text = "发送验证码";
          this.isSend = false;
        } else {
          time--;
          this.text = time + "秒后重新发送";
        }
      }, 1000);

      //发起发送验证码请求
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/emailValidCode",
        data: {
          appkey: this.appkey,
          email: this.first.email
        }
      })
        .then(res => {
          this.$toast.clear();

          this.$toast(res.data.msg);
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    //找回密码
    commit() {
      if (!Testing.Testing(this.userInfo)) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/retrievePassword",
        data: {
          appkey: this.appkey,
          phone: this.userInfo.phone,
          password: this.userInfo.password
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == "L001") {
            this.$router.push({ name: "登录" });
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    }
  }
};
</script>

<style lang="less" scoped>
.list {
  width: 90%;
  margin: 0 auto;
  .button {
    width: 70%;
    margin: 0 auto;
  }
}
.else{
  width: 90%;
  margin: 0 auto;
.v-button{
  width: 70%;
    margin: 0 auto;
}
}
</style>
